.carousel {
  overflow: hidden;

  &-indicators {
    margin-bottom: 38px;

    li {
      border-radius: 12px;
      transition: all .6s ease
    }
    
    .active {
      width: 44px;
    }
  }

  &-inner {
    margin-bottom: 0;
  }

  &-control {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    height: 100%;

    &-prev,
    &-next {
      position: unset;
      width: 10%;
    }

    &-prev {
      margin-left: -5%;
    }

    &-next {
      margin-right: -5%;
    }
  }
}

@media (max-width: 767px) {
  .carousel {
    &-indicators {
      margin-bottom: 8px;
    }
  }
}
